.about {
  display: flex;
  column-gap: 4%;
  max-width: 1440px;
  margin-left: calc(10% - 40px);
  margin-right: calc(10% - 40px);
  padding-top: 80px;
  padding-bottom: 80px;
}

.about__picture {
  align-items: center;
  max-width: 450px;
  max-height: 450px;
  border-radius: 50%;
}

.about__info-container {
  padding-top: 51px;
  max-width: 600px;
  flex-shrink: 1;
}

.about__title {
  font-family: "Roboto Slab", sans-serif;
  font-weight: 400;
  font-size: 40px;
  line-height: 1.15;
  color: #1a1b22;
}

.about__text {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: 18px;
  line-height: 24px;
  padding-top: 24px;
}

@media screen and (max-width: 768px) {
  .about {
    padding-top: 60px;
    padding-bottom: 60px;
    /* column-gap: 3%; */
  }

  /* .about__image-author {
    max-width: 232px;
    max-height: 232px;
  } */

  .about__info-container {
    padding-top: 0px;
    max-width: 424px;
  }

  .about__title {
    font-size: 1.875rem;
    line-height: 1.33;
  }

  .about__text {
    font-size: 1rem;
    padding-top: 16px;
  }
}

@media screen and (max-width: 480px) {
  .about {
    display: flex;
    flex-direction: column;
    margin-left: 16px;
    margin-right: 16px;
    padding-top: 32px;
    padding-bottom: 128px;
    align-items: center;
  }

  .about__info-container {
    max-width: 100%;
    padding-top: 24px;
  }

  .about__avatar-author {
    max-width: 272px;
    max-height: 272px;
  }

  .about__text {
    font-size: 1.125rem;
  }
}

@media screen and (max-width: 295px) {
  .about {
    padding-bottom: 100px;
  }

  .about__avatar-author {
    max-width: 265px;
    max-height: 265px;
  }

  .about__title {
    font-size: 1.5rem;
  }
  .about__text {
    font-size: 0.875rem;
  }
}
